.messageWrap{
    box-sizing: border-box;
    width: 100%;
    padding: 0 10px 20px 10px;
}

.messageItem{
    display: flex;
    flex-flow: column nowrap;
    margin-right: 10px;
    float: right;
    border-radius: 15px;
    padding: 10px;
    max-width: 80%;
    word-break: break-word;
}

.message{
    padding: 4px 8px;
    background-color: #29384B;
    border-radius: .4em;
    color: #fff;
}


.timestamp{
    font-size: 10px;
    padding: 4px 8px;
    color: #999;
    align-self: flex-end;
}